var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _vm._m(0)}
var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"home body-main border-body bg-white ql-align-articles px-3"},[_c('div',{staticStyle:{"margin-left":"50px","margin-right":"50px"}},[_c('h1',{staticStyle:{"color":"rgb(0,102,204)"},attrs:{"span":""}},[_vm._v("\n      Keynote Speakers\n    ")]),_c('br'),_c('h3',{staticStyle:{"margin-top":"10px","margin-bottom":"10px"}},[_c('strong',[_vm._v(" Keynote Talk Ⅰ\n        ")])]),_c('br'),_c('img',{staticClass:"card-im-left",attrs:{"src":require("./../../../src/images/4MingmingZhang.png"),"width":"175","height":"222"}}),_c('br'),_c('h4',[_c('strong',[_vm._v(" Dr. Mingming Zhang, Southern University of Science and Technology, China")])]),_c('h4',[_c('strong',[_vm._v("Title: Task-oriented rehabilitation techniques with robot-assisted information feedback")])]),_c('h5',[_c('p',{staticStyle:{"text-align":"justify"}},[_c('strong',[_vm._v("Abstract:")]),_vm._v("\n      Individuals with physical disabilities are often restricted to perform activities of daily life (ADLs). While efficacy of bilateral training has been demonstrated in improving physical coordination of human limbs, few robots have been developed in simulating people’s ADLs integrated with task-specific force field control. This study sought to develop a bilateral robot for better task rendering of general ADLs (gADLs), where gADL-consistent workspace is achieved by setting linear motors in series, and haptic rendering of multiple bimanual tasks (coupled, uncoupled and semi-coupled) is enabled by regulating force fields between robotic handles. Experiments were conducted with human users, and our results present a viable method of a single robotic system in simulating multiple physically bimanual tasks. The proposed robotic system is expected to be serving as a coordination training device, and its clinical efficacy will be also investigated. In this task, other relevant robot-assisted rehabilitation techniques will also be briefly introduced.\n    ")]),_c('p',{staticStyle:{"text-align":"justify"}},[_c('strong',[_vm._v("Biography:")]),_vm._v("\n      Mingming Zhang, IEEE Senior Member, Principal Investigator of Brain-Robot Rehabilitation Technology Lab, Department of Biomedical Engineering, Southern University of Science and Technology (Shenzhen, China). His main research interests include tactile sensing and feedback technology, intelligent control and human-computer interaction, wearable exoskeleton rehabilitation robots, and EMG/EEG based pattern recognition algorithms. He received the Ph.D. degree in Mechanical Engineering from the University of Auckland, New Zealand, in 2015. From 2015 to 2016, he worked as a postdoc research fellow in the Department of Mechanical Engineering at the University of Auckland. From early 2017 to 2018, he worked at CASTIOT LIMITED (Auckland) as a vice president in developing lower limb rehabilitation robotics. He was also employed as a visiting research fellow at the University of Auckland. He has authored over 50 academic papers published on peer-reviewed international journals in the fields of robotics, intelligent control, neural rehabilitation, and biomechanics, including IEEE Transactions on Industrial Electronics, IEEE Transactions on Biomedical Engineering, Journal of NeuroEngineering and Rehabilitation, Journal of Biomechanics, etc. Over 30 patents have been applied or authorized as well. He also authored an academic book published by Institution of Engineering and Technology.\n      ")])]),_c('br'),_c('h3',{staticStyle:{"margin-top":"10px","margin-bottom":"10px"}},[_c('strong',[_vm._v(" Keynote Talk II\n        ")])]),_c('br'),_c('img',{staticClass:"card-im-left",attrs:{"src":require("./../../../src/images/2DongWang.png"),"width":"175","height":"222"}}),_c('br'),_c('h4',[_c('strong',[_vm._v(" Prof. Dong Wang, Dalian University of Technology, China")])]),_c('h4',[_c('strong',[_vm._v("Title: Distributed Optimization based on multiagent systems")])]),_c('h5',[_c('p',{staticStyle:{"text-align":"justify"}},[_c('strong',[_vm._v("Abstract:")]),_vm._v("\n      In the past years, distributed optimization based on multiagent systems has received considerable attention due to its wide applications in smart grid, resource allocation and machine learning. Common features of these examples are that there is no centralized center involved and the resources, such as sensing, communication and computation, are usually connected by the networks and communicate in the local networks, which is called distributed algorithms. In this talk, I will first give some concepts related to distributed optimization and communication model. Then, I will introduce several recent works on distributed optimization. Application to economic dispatch problems in smart grids will also be discussed.\n    ")]),_c('p',{staticStyle:{"text-align":"justify"}},[_c('strong',[_vm._v("Biography:")]),_vm._v("\n      Dong Wang received the B.Sc. degree in automation and the M. Eng. degree in control theory and control engineering from the Shenyang University of Technology, Shenyang, China, in 2003 and 2006, respectively, and Ph.D. degree in control theory and control engineering from the Dalian University of Technology, China, in 2010. Since 2010, he has been with the Dalian University of Technology, where he is currently a professor with the School of Control Science and Engineering. His current research interests include multiagent systems, distributed optimization, fault detection and switched systems. He is the author or co-author of over 100 publications, which have been appeared in IEEE Transactions Journals and in conference proceedings. He is appointed as the 2019 Changjiang Young Scholars programs and was a recipient of the second prize of Wuwenjun Artificial Intelligence Science and Technology in 2017. Dr. Wang is Associate Editors of Information Sciences and Neurocomputing.\n      ")])]),_c('br'),_c('h3',{staticStyle:{"margin-top":"10px","margin-bottom":"10px"}},[_c('strong',[_vm._v(" Keynote Talk III\n        ")])]),_c('br'),_c('img',{staticClass:"card-im-left",attrs:{"src":require("./../../../src/images/3HuihuanQian.png"),"width":"175","height":"222"}}),_c('br'),_c('h4',[_c('strong',[_vm._v(" Dr. Huihuan Qian, Chinese University of Hong Kong, China")])]),_c('h4',[_c('strong',[_vm._v("Title: Intelligent Sailing Robots Towards Marine Surface Exploration")])]),_c('h5',[_c('p',{staticStyle:{"text-align":"justify"}},[_c('strong',[_vm._v("Abstract:")]),_vm._v("\n      To meet the continuously increasing need in marine surface exploration, intelligent robots with long-range capability is crucial to reduce risk to human participants and increase efficiency. This talk will present the effort in intelligent sailing robots at different scales. Starting from small-size sailboats in controlled wind field, we model the propelling thrust by force polar diagram (FPD) and control the robot to enhance its mobility. To explore for long-range capability, we extend research effort to a large-size sailboat OceanVoy. Hybrid scheme combining Pseudo-Spectral (PS) optimal control on rudder and Extreme Seeking Control (ESC) on sail is developed to improve the performance. Furthermore, we enhance V-stability based control and reduce the control frequency to save energy. Around 1200km experiment is accumulated.\n    ")]),_c('p',{staticStyle:{"text-align":"justify"}},[_c('strong',[_vm._v("Biography:")]),_vm._v("\n      Prof. Qian received his Ph.D. in Automation and Computer-Aided Engineering from the Chinese University of Hong Kong, and thereafter worked as Research Assistant Professor there. He is currently Assistant Professor at the School of Science and Engineering in the Chinese University of Hong Kong, Shenzhen. He also serves as Vice President in Shenzhen Institute of Artificial Intelligence and Robotics for Society (AIRS). His research interest include design and control of robots and intelligent system, and currently focus on marine surface robotics. He has received the Best Transaction Paper Award from IEEE/ASME Transactions on Mechatronics in 2011 with his colleagues, based on the research of steering interface for omni-directional vehicles. With his postgraduate/undergraduate students, he has received a number awards recently, including IROS2020 Best Application Paper Finalist, ROBIO2019 Best Paper Finalist, RCAR2019 Best Paper Finalist, WCICA2018 Best Student Paper Finalist. He serves as an Associate Editor in IEEE International Conference on Robotics and Automation (ICRA) and an Area Chair in Robotics: Science and Systems (RSS). \n      ")])]),_c('br'),_c('h3',{staticStyle:{"margin-top":"10px","margin-bottom":"10px"}},[_c('strong',[_vm._v(" Keynote Talk IV\n        ")])]),_c('br'),_c('img',{staticClass:"card-im-left",attrs:{"src":require("./../../../src/images/1keynote.jpeg"),"width":"175","height":"222"}}),_c('br'),_c('h4',[_c('strong',[_vm._v(" Prof. Xingguang Duan, Beijing Institute of technology, China")])]),_c('h4',[_c('strong',[_vm._v("Title: Robotic System for Percutaneous Procedures: Key Technologies, Applications, and Challenges")])]),_c('h5',[_c('p',{staticStyle:{"text-align":"justify"}},[_c('strong',[_vm._v("Abstract:")]),_vm._v("\n      Surgical robot is a modern medical research topic which integrates medicine, biomechanics, computer vision, artificial intelligence, et al. Robot-assisted percutaneous puncture technology not only has important theoretical significance, but also can effectively improve the accuracy and quality of surgical operations in clinic. Problems in traditional operations such as long operation time, fatigue of doctors, lack of visual navigation, exposure to radiation are overcome. The report focuses on robotic systems for percutaneous procedures. Key technologies including sensing, navigation, and control are analyzed. System applications for craniomaxillofacial and lung puncture are specially introduced. Challenging issues and future trends are elaborated.\n    ")]),_c('p',{staticStyle:{"text-align":"justify"}},[_c('strong',[_vm._v("Biography:")]),_vm._v("\n      Xingguang Duan is currently a professor at School of Mechatronical Engineering, Beijing Institute of technology. He is the deputy director of the Key Laboratory of Biomimetic Robots and Systems, and also a PI of Beijing Advanced Innovation Center for Intelligent Robots and Systems, and Institute of Engineering Medicine. Prof. Duan is the expert member for key special project on intelligent robot of the Ministry of Science and Technology (MOST), and the expert in compiling guidelines of the special project on major scientific instruments of the MOST. He is also the Leading talent for Taishan industry, and membership of multiple academic organizations. His research interests include design of the surgical robot system, robotic perception and understanding of environment, navigation and planning of robotics, human-robot cooperative intelligent control, etc. He has over 20 projects supported by the National Natural Science Foundation, the National High Technology Research and Development Program, the state support program, etc. He has published more than 80 academic papers and authorized 40 patents. He has won the Science and Technology Progress Award of Machinery Industry, National Defense Science and Technology Award, etc.\n    ")])])])])}]

